import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import navData from '../assets/navData'
import cuetlogo from '../assets/images/cuet-img/cuetlogo.png'

const Navbar = ({ title }) => {
    const [menuOpen, setMenuOpen] = React.useState(false)
    const toggleMe = (e) => {
        e.preventDefault()
        setMenuOpen(!menuOpen)
    }
    return (
        <Wrapper>
            <nav className={menuOpen ? 'active' : ''}>
                <div className="heading">
                    <img src={cuetlogo} alt="CUET logo" />
                    <h3>{title}</h3>
                </div>
                <ul>
                    {
                        navData.map(data => (
                            <li><Link to={`/${data.name}`}>{data.name}</Link></li>
                        ))

                    }
                </ul>

                <div className="info">
                    <form action="" className='search-form'>
                        <input type="text" placeholder='search here' />
                        <button><i className="fas fa-search"></i></button>
                    </form>
                    <ul>
                        <li><Link to='/apply'><i className="fas fa-pen"></i> Apply</Link></li>
                        <li><Link to='/giving'><i className="fas fa-gift"></i> giving</Link></li>
                        <li><Link to='/news'><i className="fas fa-newspaper"></i> news</Link></li>
                    </ul>
                </div>

                <button className="icon" onClick={toggleMe}>

                    {
                        menuOpen ? <span><i className="fas fa-angle-left"></i></span> : <span><i className="fas fa-bars"></i></span>
                    }
                </button>
            </nav>

        </Wrapper >
    )
}

const Wrapper = styled.section`

nav{
    /* position: absolute; */
    background-color: var(--clr-primary-2);
    padding: 20px;
    width: 80px;
    height: 80px;
    /* display: flex; */
    /* justify-content: center; */
    /* align-items: center; */
    border-radius: 3px;
    box-shadow:0 2px 5px rgba(0,0,0,0.3);
    transition: all 0.6s linear;
    position: fixed;
}
nav .heading{
    padding: 10px;
}
nav .heading img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100px;
    height: 100px;
    padding:5px;
}
nav .heading h3{
    font-size: 20px;
    color:var(--clr-primary-9);
    font-weight: 800;
    text-align: center;
    padding-bottom: 15px;
    border-bottom: 3px solid var(--clr-primary-1);
}
nav .heading{
    width: 0;
    opacity: 0;
    transition: all 0.6s linear;
}
nav.active .heading{
    width: 100%;
    opacity: 1;
}
 
nav.active{
    width: 400px;
    height: 100vh;
}
nav ul{
    /* display: flex; */
    padding: 0;
    margin: 0;
    width: 0;
    transition: width 0.6s linear;
   
}
nav.active ul{
    width: 100%;
}
nav ul li{
    transform: rotateY(0);
    opacity: 0;
    transition: all 0.6s linear;
    line-height: 40px;
}
nav.active ul li{
    transform: rotateY(360deg);
    opacity: 1;
}
nav ul li a{
    position: relative;
    margin: 0 10px;
    color: var(--clr-primary-10);
    text-transform:uppercase;
    font-size: 20px;
    font-weight: 700;
}
nav ul li:hover{
    background-color: var(--clr-primary-3);
}
nav .info{
    margin-top: 5px;
    padding: 5px 0;
    background-color: var(--clr-primary-3);
}
nav .info .search-form{
    text-align: center;
}
.search-form input, .fa-search, .search-form button{
    outline: 0;
    background: transparent;
    border: 0;
    border-bottom: 1px solid var(--clr-primary-10);
    color: var(--clr-primary-10);
    text-align: center;
    transition: all 0.5s ease;
    text-transform: uppercase;
}

.fa-search{
    border-bottom: 0;
}
nav .info ul{
    margin-top: 5px;
    display: flex;
    justify-content: space-around;
}

nav .info ul li a{
    color: var(--clr-primary-10);
    font-size: 15px;
}

.icon{
    background-color: var(--clr-primary-10);
    border: 0;
    cursor: pointer;
    padding: 0;
    height: 30px;
    width: 30px;
    position: absolute;
    top: 20px;
    right: 20px;
}
.icon:focus{
    outline: 0;
}

@media screen and (max-width:575px){
    nav.active{
        width: 100vw;
        height: 100vh;
    }

    nav ul li{
        line-height: 25px;
    }

    nav ul li a{
        font-size: 15px;
        font-weight: 500;
    }
}

`
export default Navbar
